import React from "react"
import { useState } from "react"

const About = () => {
	const [isVisible, setstate] = useState(false)

	const toggleVisible = () => {
		setstate(!isVisible)
	}
    
	return (
        
        <div className='flex max-w-3xl h-auto mx-auto mt-8 mb-8'>
            <div className='w-full h-full flex flex-col place-items-center'>
                <div className='flex justify-between mb'>
                    <div className='text-blue-400 text-4xl font-bold mt-16'>About Me</div>
                </div>
                <div className='flex bg-white shadow overflow-hidden sm:rounded-md mt-8'>
                    <img src="data:image/jpeg;base64," alt="" width='192px' height='96px' />
                    <div className='flex justify-between mb-16 ml-8 mr-8 mt-4'>
                        <div className='text-black text-xl text-justify'>
                            Hi! My name is Benedictus Visto Kartika, call me Visto. I am a computer science undergraduate at BINUS University. I actively participate in competitive programming competitions. I am interested in computer science especially in AI and game development.
                        </div>
                    </div>
                </div>
            </div>
        </div>
	)
}

export default About
